.--team-leaders {
  .svg {
    position: absolute;

    &--1 {
      left: 0;
      top: 18%;
      width: 40vw;
    }

    &--2 {
      right: 0;
      top: 5%;
      width: 15vw;
    }

    &--3 {
      width: 100vw;
      z-index: -1;
      top: -50%;
    }

    &--4 {
      right: 0;
      top: -20%;
      width: 45vw;
    }
  }

  .--title {
    font-family: "gill sans", sans-serif !important;
    text-transform: uppercase;
    color: #283a97;
    font-size: 5vw;
    letter-spacing: 0.2em;
  }

  .--desc {
    margin-top: 5vw;
    font-family: "gill sans medium", sans-serif !important;
    color: #283a97;
    font-size: 2.4vw;
    letter-spacing: 0.1em;
    line-height: 2em;
    color: #676ab1;
    white-space: pre-line;
  }

  .--sub-title {
    margin-top: 5vw;
    font-family: "gill sans medium", sans-serif !important;
    color: #283a97;
    font-size: 3.8vw;
    letter-spacing: 0.1em;
    line-height: 1.5em;
    color: #676ab1;
    white-space: pre-line;
    text-transform: uppercase;
  }

  .header {
    padding: 23vw 0 35vw;
  }

  .team-leader {
    background-color: #9192c2;
    border: 0;
    border-radius: 0;
    font-size: 3vw;
    display: flex;
    flex-direction: column;
    width: 85%;
    margin: 0 auto;

    padding: 5%;

    &__container {
      &:not(:first-child) {
        margin-top: 8vw;
      }
    }

    &__link {
      text-decoration: none;
    }

    &__name {
      font-family: "gill sans", sans-serif !important;
      white-space: pre-line;
      line-height: 1.5em;
      letter-spacing: 0.2em;
    }

    &__position {
      font-family: "gill sans medium", sans-serif !important;
      white-space: pre-line;

      line-height: 1.5em;
      letter-spacing: 0.2em;
    }

    &__sub-position {
      font-family: "gill sans medium", sans-serif !important;
      line-height: 1.5em;
      letter-spacing: 0.2em;
      font-size: 0.7em;
      text-align: end;
    }
  }

  .join-our-team {
    margin-top: 50vw;
    padding-bottom: 20vw;

    .join-image {
      &__image {
        width: 50%;
      }
    }

    .--title {
      margin-top: 10vw;
      font-size: 4.2vw;
      color: #676ab1;
    }

    .--desc {
      font-size: 2.5vw;
    }

    .--btn-send-resume {
      background: #283a97;
      display: flex;
      border-radius: 0;
      border: 0;
      margin-top: 10vw;

      a {
        color: white;
        text-decoration: none;
        font-family: "gill sans", sans-serif !important;
        text-transform: uppercase;
        font-size: 3.3vw;
        letter-spacing: 0.2em;
        line-height: 2em;

        padding: 0.8em 13vw;
      }
    }
  }
}

